.contacts {
  display: flex;
  flex-direction: column;
  padding-top: 41px;
  width: 100%;

  background: url("../img/bg-zigzag-line.svg") left top repeat-x;
  background-size: auto 6px;
}

.contacts__title {
  margin: 0;
  padding: 0 29px;

  color: $black;
  font-size: 18px;
  line-height: 30px;
}

.contacts__list {
  padding: 23px 27px 35px;

  line-height: 30px;
}

.contacts__field {
  width: 29%;
  padding: 0;
  padding-bottom: 28px;

  vertical-align: top;
}

.contacts__value {
  padding: 0;

  vertical-align: top;
}

.contacts__email {
  color: inherit;

  border-bottom: 1px solid $color-secondary;

  &:hover {
    color: $email-hover;
    border-color: $email-hover;
  }

  &:focus {
    outline: none;
    color: $email-hover;
    opacity: 0.3;
  }
}

.contacts__map {
  min-height: 457px;
  width: 100%;
  position: relative;

  background-image: url("../img/icon-map-pin.svg"), url("../img/map-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% calc(50% - 33px), center;
  background-size: 66px 101px, 100% auto;

  @media (min-resolution: $retina-dppx) {
    background-image: url("../img/icon-map-pin.svg"), url("../img/map-mobile@2x.jpg");
  }
}

.contacts__map-pin {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(calc(-50% + 77px), calc(-50% - 33px));
}

.contacts__image {
  display: block;
}

.contacts__write {
  margin: 36px 30px;
  padding: 13px 47px 13px 48px;
}

@media (min-width: $tablet-width) {
  .contacts {
    position: relative;
    align-items: flex-start;
    width: 100%;
    padding-top: 77px;
    padding-bottom: 57px;

    background-position: left top;
    background-size: auto 15px;

    &::before {
      content: "";
      position: absolute;
      top: 62px;
      right: 53px;
      width: 47px;
      height: 81px;
      background: url("../img/icon-flag.svg") no-repeat;
    }
  }

  .contacts__title {
    padding-left: 45px;

    font-size: 43px;
    line-height: 50px;
  }

  .contacts__list {
    width: 100%;
    padding: 47px 59px 52px 46px;

    font-size: 17px;
  }

  .contacts__list tbody {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 97%;
  }

  .contacts__field {
    width: 25%;
  }

  .contacts__value {
    width: 247px;
  }

  .contacts__write {
    align-self: center;
    width: 230px;
    height: 52px;
    margin: 0;
    margin-top: 48px;
    padding: 14px 0;

    font-size: 17px;
  }

  .contacts__map {
    background-image: url("../img/icon-map-pin.svg"), url("../img/map-tablet@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/icon-map-pin.svg"), url("../img/map-tablet@2x.jpg");
    }
  }
}

@media (min-width: $desktop-width) {
  .contacts {
    position: relative;
    padding-top: 108px;
    padding-bottom: 105px;
    padding-left: 220px;

    background-size: 1052px 23px;
    background-position: 23px 0;

    &::before {
      top: 155px;
      left: 113px;
    }
  }

  .contacts__title {
    padding: 0;
    padding-top: 59px;

    font-size: 53px;
  }

  .contacts__write {
    align-self: flex-start;
    margin: 0;
    margin-top: 74px;
    margin-left: 6px;
    padding-top: 16px;
  }

  .contacts__list {
    padding: 0;
    padding-top: 46px;
  }

  .contacts__list tbody {
    flex-direction: column;
  }

  .contacts__field {
    width: 23.3%;
  }

  .contacts__map {
    width: 525px;
    position: absolute;
    top: 93px;
    right: 51px;

    background-image: url("../img/icon-map-pin.svg"), url("../img/map-desktop@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/icon-map-pin.svg"), url("../img/map-desktop@2x.jpg");
    }
  }
}
